header{
    width: 100%;
    background-color: white;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
    position: fixed;
    border: 1px solid black;
}

.list{
    list-style-type: none;
    display: inline-flex;
    padding: 15px;
}

.list>*{
    margin-right: 20px;
}