#contact{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    text-align: left;
    padding: 50px;
}

#img-holder{
    width: 100%;
}

#img-holder img{
    width: 100%;  
}

#contact-section{
    width: 100%;
    display: flex;
    padding: 50px;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}
#title-text{
    font-size: 80px;
    font-weight: bold;
}
