#about{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    text-align: center;
    
}

#title-text{
    font-size: 80px;
    font-weight: bold;
}

#about-cards{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width:100%
}

.cards{
    width: calc(100%/3);
    padding: 80px;
    
}

