#home{
    background-color: lightgray;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    text-align: left;
    padding: 50px;
}
#img-holder{
    width: 100%;
}
#home-section{
    width: 100%;
    display: flex;
    padding: 30px;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}
#home-para {
    padding: 20px 0;
    line-height: 1rem;
    letter-spacing: 2px;
}
.para-style{
    color: black; 
}